import React, { useRef, useEffect } from 'react';
import classNames from 'classnames';
import dedent from 'dedent';
import './highlight.scss';
import 'highlight.js/styles/tomorrow-night.css';
import hljs from 'highlight.js/lib/highlight';
// import useHighlight from './useHighlight';

const Highlight = props => {
  // prettier-ignore
  const {
    tag: Tag,
    className,
    children,
    lang,
    ...attributes
  } = props;

  const highlightRef = useRef();
  const codePreview = dedent(children);
  const classes = {
    Highlight: classNames(`Highlight`, className),
    HighlightCode: classNames(`hljs`, lang),
  };

  useEffect(() => {
    if (highlightRef) {
      hljs.highlightBlock(highlightRef.current);
    }
  }, [highlightRef]);

  return (
    <Tag {...attributes} className={classes.Highlight}>
      <pre>
        <code ref={highlightRef} className={classes.HighlightCode}>
          {codePreview}
        </code>
      </pre>
    </Tag>
  );
};

Highlight.defaultProps = {
  tag: 'div',
  lang: 'html',
};

export default Highlight;
